import React, { useState, FC } from 'react';
import clsx from 'clsx';
import { AppBar, Box, Drawer, Hidden, IconButton, Link, Toolbar, Paper, Typography } from '@material-ui/core';
import { Menu as MenuIcon, Close as CloseIcon } from '@material-ui/icons';
import makeStyles from '@material-ui/core/styles/makeStyles';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100vw',
    height: '100vh',
    flexGrow: 1
  },
  spacer: {
    flexGrow: 1
  },
  appbar: {
    background: 'rgba(0, 0, 0, 0.1)'
  },
  title: {
    flexGrow: 3
  },
  navDesktop: {
    flexGrow: 2,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-around'
  },
  link: {
    cursor: 'pointer',
    fontWeight: 600,
    borderRadius: 4,
    padding: '8px 16px',
    color: 'white',
    position: 'relative',
    '&:hover': {
    },
    '&:before': {
      content: "''",
      borderRadius: 1,
      position: 'absolute',
      width: '100%',
      height: 2,
      bottom: 0,
      left: 0,
      background: '#FFF',
      // background: 'linear-gradient(90deg, rgba(108,222,228,1) 0%, rgba(189,253,45,1) 50%, rgba(108,222,228,1) 100%)',
      // boxShadow: '0px 0px 1px 1px rgba(108,222,228,.5),-0px -0px 1px 1px rgba(189,253,45,.5),0px 0px 2px 2px rgba(108,222,228,.5)',
      visibility: 'hidden',
      transform: 'scaleX(0)',
      transition: 'all 0.3s ease-in-out 0s'
    },
    '&:hover::before': {
      visibility: 'visible',
      transform: 'scaleX(1)'
    }
  },
  menuButton: {
    color: 'white'
  },
  drawerPaper: {
    minHeight: 300,
    background: 'transparent',
    position: 'relative',
    '&::after': {
      content: "''",
      width: '100vw',
      height: 300,
      background: '#383a40',
      position: 'absolute',
      top: 0,
      zIndex: -1,
      filter: 'blur(8px)'
    }
  },
  closeIconBtn: {
    color: 'white',
    position: 'absolute',
    top: 12,
    right: 12
  },
  closeIcon: {
    fontSize: 24
  },
  mobileList: {
    minHeight: 220,
    margin: 10,
    marginTop: 48,
    padding: 0,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-around',
    '& li': {
      listStyle: 'none',
    },
    '& li span': {
      color: 'white',
      fontSize: '1rem',
      padding: '0 20px'
    }
  },
  item1: {
    animationDelay: '0.2s',
    animationDuration: '.9s'
  },
  item2: {
    animationDelay: '.5s',
    animationDuration: '.9s'
  },
  item3: {
    animationDelay: '.8s',
    animationDuration: '.9s'
  },
  item4: {
    animationDelay: '1.1s',
    animationDuration: '.9s'
  },
  main: {
  }
}));

export interface LayoutProps {

};

const Layout: FC<LayoutProps> = ({ children }) => {
  const classes = useStyles();

  const history = useHistory();

  const [open, setOpen] = useState(false);

  const openDrawer = () => {
    setOpen(true);
  };

  const closeDrawer = (event: React.KeyboardEvent | React.MouseEvent) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }
    setOpen(false);
  };

  const linkTo = (path: string, close?: boolean) => (event: React.MouseEvent<HTMLElement>) => {
    if (close && open) setOpen(false);
    history.push(path);
  };

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appbar}>
        <Toolbar>
          <Typography className={classes.title}>毕竟几人真得鹿， 不知终日梦为鱼</Typography>
          <Box className={classes.navDesktop}>
            <Hidden smDown>
              <Link className={classes.link} underline="none" onClick={linkTo('/home')} >首页</Link>
              <Link className={classes.link} underline="none" onClick={linkTo('/about')} >杂记</Link>
              <Link className={classes.link} underline="none" onClick={linkTo('/other')} >闲谈</Link>
              <Link className={classes.link} underline="none" onClick={linkTo('/test')} >妙趣</Link>
            </Hidden>
          </Box>
          <div className={classes.spacer} />
          <Hidden mdUp>
            <IconButton edge="end" className={classes.menuButton} onClick={openDrawer}>
              <MenuIcon />
            </IconButton>
          </Hidden>
        </Toolbar>
      </AppBar>
      <Hidden mdUp>
        <Drawer
          transitionDuration={300}
          anchor="top"
          open={open}
          classes={{ paper: classes.drawerPaper }}
          onClose={closeDrawer}
        >
          <IconButton className={classes.closeIconBtn} onClick={closeDrawer}>
            <CloseIcon className={classes.closeIcon} />
          </IconButton>
          <ul className={classes.mobileList}>
            <li className={clsx(classes.item1, "animated fadeInDown")}>
              <span onClick={linkTo('/home', true)}>首页</span>
            </li>
            <li className={clsx(classes.item2, "animated fadeInDown")}>
              <span onClick={linkTo('/about', true)}>杂记</span>
            </li>
            <li className={clsx(classes.item3, "animated fadeInDown")}>
              <span onClick={linkTo('/other', true)}>闲谈</span>
            </li>
            <li className={clsx(classes.item4, "animated fadeInDown")}>
              <span onClick={linkTo('/test', true)}>妙趣</span>
            </li>
          </ul>
        </Drawer>
      </Hidden>
      <Paper className={classes.main}>{children}</Paper>
    </div>
  )
};

export default Layout;